<template>
  <div>
    <div id="box1" class="box">
      <ul>
        <li>
          <el-input v-model="input" placeholder="课程ID/名称"></el-input>
        </li>
        <li>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
        <li>
          <el-button type="primary">查询</el-button>
        </li>
        <li>
          <el-button>重置</el-button>
        </li>
      </ul>
    </div>
    <div id="box2" class="box">
      <div>
        <el-button type="primary">+添加材料分析题</el-button>
      </div>
      <div id="table">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="题目Id" width="120" prop="classId"></el-table-column>
          <el-table-column label="材料" prop="className"  width="270"></el-table-column>
          <el-table-column label="问题" prop="classPic"  show-overflow-tooltip></el-table-column>
          <el-table-column label="排序" prop="classTime"  show-overflow-tooltip></el-table-column>
          <el-table-column label="添加时间" prop="classOnce"  show-overflow-tooltip></el-table-column>
                   <el-table-column fixed="right" label="操作" width="120">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, tableData)"
                type="text"
                size="small"
              >移除</el-button>
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div id="page">
          <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "cont1",
  data() {
    return {
      input: "",
      options: [
        {
          value: "选项1",
          label: "课程类型"
        },
        {
          value: "选项2",
          label: "综合素质"
        },
        {
          value: "选项3",
          label: "教育教学能力"
        }
      ],
      tableData: [
        {
          classId: "2019999",
          className: "材料材料材料，内容内容内容内容，以下以下，所有作业和实战，我们针每一位学员1对1批改.....？",
          classPic: "1、问题1；2、问题2；3、问题3；",
          classTime: "1",
          classOnce: "2017-07-19 14:48:38",
        
        }, {
          classId: "2019999",
          className: "材料材料材料，内容内容内容内容，以下以下，所有作业和实战，我们针每一位学员1对1批改.....？",
          classPic: "1、问题1；2、问题2；3、问题3；",
          classTime: "1",
          classOnce: "2017-07-19 14:48:38",
        
        }, {
          classId: "2019999",
          className: "材料材料材料，内容内容内容内容，以下以下，所有作业和实战，我们针每一位学员1对1批改.....？",
          classPic: "1、问题1；2、问题2；3、问题3；",
          classTime: "1",
          classOnce: "2017-07-19 14:48:38",
        
        }
      ],

      value: "",
      multipleSelection: []
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },   deleteRow(index, rows) {
      rows.splice(index, 1);
    },    handleClick(row) {
        console.log(row);
      }
  }
};
</script>
<style>
.box {
  padding: 20px 10px;
  width: 90%;
  margin: 20px auto;
  background: white;
  border-radius: 5px;
}
#box1{
  
}
#box1 ul li {
  margin-left: 20px;
  list-style: none;
  display: inline-block;
}
#box2::after{
  display: block;
  content: "";
  clear: both;
}
#page {
  margin-top: 20px;
  float: right;
}
</style>s